import React, { Component } from "react";
import "./detail.scss";
import { Icon, TextareaItem, Toast } from "antd-mobile";
class Notes extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lastid: -1,
      noteValue: ""
    };
  }
  render() {
    return (
      <div className="details">
        <div className="d-header">
          <Icon type="left" onClick={this.goback.bind(this)} />
          <span className="note">ÊàëÁöÑÁ¨îËÆ∞</span>
          {/* <p>{this.state.noteValue}</p> */}
          <span onClick={this.delNote.bind(this)} className="del" />
        </div>
        <div className="noteContent">
          <TextareaItem
            onChange={this.inputChange.bind(this)}
            autoHeight
            rows={6}
            placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÁ¨îËÆ∞..."
            value={this.state.noteValue}
          />
        </div>
      </div>
    );
  }
  goback() {
    this.props.history.go(-1);
  }
  componentWillMount() {
    //ÂÖàÂà§Êñ≠‰º†ËøõÊù•ÁöÑÊúâÊ≤°Êúâid
    // console.log(this.props.match.params);
    let appNote = JSON.parse(localStorage.getItem("appNote")) || [];
    if (this.props.match.params.id) {
      let noteValue = appNote[parseInt(this.props.match.params.id - 1)].value;
      this.setState(
        {
          lastid: this.props.match.params.id,
          noteValue
        },
        () => {}
      );
    } else {
      if (!appNote) {
        this.setState({
          lastid: 1
        });
      } else {
        //ËøõÊù•ÁöÑÊó∂ÂÄôÂà§Êñ≠Â¶ÇÊûúÊòØÊñ∞Â¢ûÂ∞±Êõ¥Êñ∞lastid
        this.setState({
          lastid: appNote.length + 1
        });
      }
    }
  }
  filterData(data) {
    data.map((item, index) => {
      item.id = index + 1;
    });
  }
  //Âà†Èô§Á¨îËÆ∞
  delNote() {
    //Â≠òÂú®id
    if (this.props.match.params.id) {
      let appNote = JSON.parse(localStorage.getItem("appNote"));
      appNote.splice(this.props.match.params.id - 1, 1);
      this.filterData(appNote);
      localStorage.setItem("appNote", JSON.stringify(appNote));
      //ËøõË°åÈáçÊñ∞ÊéíÂ∫è id ÂøÖÈ°ªÂØπÂ∫îindex+1
      Toast.success("Âà†Èô§ÊàêÂäü");
      this.props.history.push("/note");
    } else {
      Toast.success("Ê≤°ÊúâÂèØÂà†Èô§ÁöÑÁ¨îËÆ∞");
    }
  }
  inputChange(e) {
    let value = e;
    //ÊãøÂà∞Ëøô‰∏™Êï∞ÁªÑÁöÑÊúÄÂêé‰∏Ä‰∏™id‰∏éÁé∞Âú®ÁöÑidÂØπÊØî -- Âõ†‰∏∫‰ª¨ÊòØÂÜôÂú®change‰∫ã‰ª∂ÁöÑ
    //ËøôÊòØnewÂè™Ë¶ÅÊõ¥Êñ∞ÊúÄÂêé‰∏Ä‰∏™Â∞±Ë°å
    //ÂàõÂª∫Êñ∞ÁöÑÁ¨îËÆ∞
    //ËØ¥ÊòéÂè™Ë¶ÅÊõ¥Êñ∞Â∞±Ë°å
    this.setState({
      noteValue: value
    });
    let appNote = JSON.parse(localStorage.getItem("appNote")) || [];
    if (appNote.length) {
      //Â≠òÂú®Ëøô‰∏™Â∞±Ëµ∞Êõ¥Êñ∞ÁöÑ
      if (this.props.match.params.id) {
        appNote[this.props.match.params.id - 1].value = value;
      } else {
        if (parseInt(this.state.lastid) === parseInt(appNote[appNote.length - 1].id)) {
          appNote[appNote.length - 1].value = value;
        } else {
          console.log("Êñ∞Â¢ûËøô‰∏ÄÊ≠•");
          this.setState({
            lastid: appNote.length + 1
          });
          appNote.push({
            id: appNote.length + 1,
            value
          });
        }
      }
    } else {
      appNote.push({
        id: 1,
        value
      });
    }

    localStorage.setItem("appNote", JSON.stringify(appNote));
  }
}
export default Notes;
