//   item.charpter.map(v => {
//     return <li key={v}>{v}</li>;
//   })
//   }
// Âõ†‰∏∫Â∫ï‰∏ãËøòÊúâÊ†áÁ≠æ
// console.log(item.charpter.length  ËÆ∞‰ΩèÂêéËÆ∞ÈáåÊ≤°ÊúâÊï∞ÁªÑ)
// reactÈáåËøôÈáåÂÜô‰∏ç‰∫ÜforÂæ™ÁéØ ËÄå‰∏îÁî®‰∏ç‰∫Ülength??
// console.log(item.charpter.length);Â∞±‰ºöÊä•Èîô

//ÊÄªÁªìcssÈÅáÂà∞ÁöÑÂùë
//Á¨¨‰∏ÄÁà∂Â≠êÂÖÉÁ¥†ÈÉΩÊòØÂÆö‰ΩçÁöÑ ‰ΩÜÊòØÁà∂ÂÖÉÁ¥†‰∏çÂèØËÉΩÈÄöËøáz-index‰øÆÊîπÁà∂Â≠êÂ±ÇÁ∫ßÂÖ≥Á≥ª
//Ê≤°ÊúâÊïàÊûúÁöÑ

//Ê†∑ÂºèÂΩ±ÂìçmarkdownËØ≠Ê≥ï
